           description="Informacin general del caso"></el-step>
  <el-step title="Parmetros de entrada"
           description="Captura de variables asociadas al caso"></el-step>
  <el-step title="Ejecucin"
           description="Estado de la Ejecucin"></el-step>
  <el-step title="Visualizacin de Resultados"
           description="Parmetros de salida"></el-step>
</el-steps>

</template>

<script>
export default {

  name: 'ProcessStep',
  props: [
    'activeStep'
  ]
}

</script>



